import * as React from 'react';
import { NavLink, useParams, useSearchParams } from 'react-router-dom';

import { getCN } from '@miq/utiljs';
import { IFormUpdateProps } from '@miq/formjs';
import { Button, ConfirmButton, Icons, ViewSection } from '@miq/componentjs';
import Staff, { ImgAddButtonProps, StaffImg, StaffImgAddButton } from '@miq/staffjs';

import { emitter } from '../events';
import { TSupplierOrderInstance } from '../Supplier';
import { TProduct, TProductStageData } from '../types';
import { ProductPriceDisplay, PublishedIcon } from '../components';

import { StaffProduct, TStaffProductInstance } from './models';

//#region ============================== BUTTONS

export type ProductImagesAddButtonProps = Omit<ImgAddButtonProps, 'onSuccessMulti' | 'onErrorMulti'> & {
  productInstance: TStaffProductInstance;
};

/**
 * Button component for adding images to a product
 * ```js
 * <ProductImagesAddButton productInstance={instance} onSuccess={(res: any) => onUpdate?.(res.data)} />
 * ```
 */

export const ProductImagesAddButton = (props: ProductImagesAddButtonProps) => {
  const { productInstance, onSuccess, onError, ...rest } = props;
  return (
    <StaffImgAddButton
      className="btn-primary-3"
      {...rest}
      multiple={true}
      onSuccessMulti={(resArray) => {
        const imgsArray = resArray
          .map(({ data }: any) => data?.slug)
          .filter((slug) => !productInstance.images?.includes(slug));
        return productInstance
          .update({ images: [...productInstance.images!, ...imgsArray] })
          .then((res) => onSuccess?.(res))
          .catch((err) => onError?.(err));
      }}
    />
  );
};

export const ProductDeleteButton: React.FC<
  Omit<IFormUpdateProps<TStaffProductInstance>, 'context'> & { label?: string }
> = (props) => {
  const { instance, onSuccess, onError, label } = props;

  const handleDelete = (setOpen: any) => {
    instance
      .destroy()
      .then((res: any) => {
        setOpen?.(false);
        onSuccess?.(res);
        emitter.destroy({ slug: instance.slug });
      })
      .catch((err: any) => onError?.(err));
  };
  return (
    <ConfirmButton
      renderHeader={() => <div>Supprimer '{instance.name}'</div>}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Annuler</Button>

          <Button onClick={() => handleDelete(setOpen)} className="btn-danger">
            Supprimer
          </Button>
        </div>
      )}
      render={() => <div className="p-3">Are you sure you want to delete this</div>}
      className={getCN(['product-delete-button btn-danger-3', props.className])}
    >
      <Icons.Trash />
      {label && <span className="label">{label}</span>}
    </ConfirmButton>
  );
};

//#endregion ============================== BUTTONS

export const ProductListItem = ({
  item,
  ...props
}: {
  item: TProduct;
  orderInstance?: TSupplierOrderInstance;
  stages: TProductStageData[];
  params?: URLSearchParams;
}) => {
  const [open, setOpen] = React.useState(false);

  if (!item?.slug) return null;
  const { params, orderInstance, stages = [] } = props;

  const instance = StaffProduct(item);
  const to = `${instance.slug}/${`${params}` ? `?${params}` : ''}`;
  const { supplier_item = { cost: 0 }, stage } = instance;

  const stageData = stages?.find((i) => i.value === stage);

  return (
    <ViewSection
      border
      title={
        <div className="d-flex align-itemscenter" onClick={() => setOpen(!open)}>
          <div style={{ width: 48 }}>
            <Staff.Img.Thumb src="src" {...instance.cover_data} className="rounded" />
          </div>

          <div className="ms-1 d-grid grid-md-3">
            <div className="span-md-2">
              {item.is_oos && <span className="text-sm text-danger">Out of stock</span>}
              <div className="text-sm d-flex align-items-center">
                <div className="text-ellipsis me-1 fw-bold">{item.name_truncated}</div>
                <PublishedIcon show={item.is_published || false} />

                <Button
                  className={getCN(['btn-round ms-1', instance.is_pinned && 'btn-primary-3'])}
                  style={{ width: 20, height: 20, padding: 8 }}
                  onClick={(e) => {
                    e.stopPropagation();
                    instance.update({ is_pinned: !instance.is_pinned }).then(({ data }) => {
                      emitter.update(data);
                    });
                  }}
                >
                  <Icons.PinAngle />
                </Button>
              </div>

              <div className="d-flex text-sm">
                <ProductPriceDisplay {...instance.export()} className="d-flex flex-wrap" />
                <div className="text-muted px-1">{`|`}</div>
                {supplier_item && supplier_item?.cost && (
                  <div className="text-muted">{`${supplier_item?.cost} ${orderInstance?.currency || 'USD'}`}</div>
                )}
              </div>
            </div>

            <div className="text-sm">
              <div>
                <span className="">
                  {instance?.category_data?.name || "' - '"}
                  {supplier_item && <span className="text-muted">{` | ${supplier_item.category}`}</span>}
                </span>
              </div>

              <span>{`Sizes: ${instance.size_count}`}</span>
              {stageData && <span className="text-muted">{` - ${stageData.label}`}</span>}

              {supplier_item && (
                <a className="ms-1" href={supplier_item.url} target="_blank" rel="noopener noreferrer">{`↗️`}</a>
              )}
            </div>
          </div>
        </div>
      }
      actions={
        <div className="d-flex">
          <Staff.Link to={`./${to}`} className="btn btn-square btn-primary-3">
            <Icons.PencilSquare />
          </Staff.Link>
        </div>
      }
      className="mb-1"
    >
      {open && <div className=""></div>}
    </ViewSection>
  );
};

export const ProductCardItem = ({ data, ...props }: { data: TProduct }) => {
  const instance = StaffProduct(data);

  return (
    <div className="p-card-list-item p-1 mb-1">
      <div className="d-flex align-items-center">
        <div className="p-name text-ellipsis">{instance.name_truncated}</div>
        <div className="ms-1">
          <PublishedIcon show={instance.is_published} />
        </div>
      </div>

      <div className="d-flex">
        <StaffImg.Square {...instance?.cover_data} />
        <div className="p-info ps-1">
          <ProductPriceDisplay {...instance.export()} className="d-flex flex-wrap" />
          <div className="p-category">{instance.category_data?.name}</div>
        </div>
      </div>
    </div>
  );
};

export type ProductCardListProps = {
  items: TProduct[];
  itempath: string;
};
export const ProductCardList = ({ items, itempath }: ProductCardListProps) => {
  const [params] = useSearchParams();
  const { prodSlug } = useParams();

  return (
    <div className="p-card-list-items">
      {items.map((item: TProduct) => {
        let to = `${itempath}${item.slug}/?${params}`;
        return (
          <NavLink to={to} key={item.slug} className={getCN([prodSlug === item.slug && 'active'])}>
            <ProductCardItem data={item} />
          </NavLink>
        );
      })}
    </div>
  );
};
