import * as React from 'react';
import { useParams, useSearchParams } from 'react-router-dom';

import Staff from '@miq/staffjs';
import { useRequest } from '@miq/hookjs';
import { Cart } from './models';
import { Response, truncateStr } from '@miq/utiljs';
import { Loading } from '@miq/componentjs';
import { CustomerCard } from './components';
import Form, { useForm } from '@miq/formjs';
import {
  useProductListRequest,
  ProductListFilterForm,
  SizeQuantityField,
  SizeSelectField,
  TProduct,
  ProductPriceDisplay,
} from '@shopy/storejs';
import { TOrder } from './types';

export default function CartUpdateView() {
  const [params] = useSearchParams();
  const { cartSlug } = useParams();

  const qForm = useForm({ q: '' });
  const { res: productRes } = useProductListRequest(undefined, { published: 'include', atc: 1 });

  const { res, setRes, loading, ...request } = useRequest(() => Cart({ slug: cartSlug }).retrieve(), {
    refreshDeps: [cartSlug],
    // onSuccess: ({ data }) => form.setValues(mapData2Values(data)),
    onError: ({ response }) => {
      // if (response?.status === 404) return navigate('../');
    },
  });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  if (loading) return <Loading />;

  const cart = Cart(r.data);
  const products = new Response(productRes).items || [];

  console.log(r.data);

  return (
    <Staff.View title="Modifier une commande" className="">
      <div className="d-grid grid-md-6 gap-2">
        <div className="span-md-3">
          <Staff.Section
            title="Ajouter des produits"
            header={
              <>
                <ProductListFilterForm />
              </>
            }
          >
            {products
              .filter((i) => !cart.data.products.includes(i.slug))
              .map((product) => {
                return (
                  <div className="" key={product.slug}>
                    <ProductAddForm
                      item={product}
                      onItemClick={(product, size, qty) => {
                        return cart.addItem(product.slug!, size, qty).then((r) => {
                          setRes(r);
                          return r;
                        });
                      }}
                    />
                  </div>
                );
              })}
          </Staff.Section>
        </div>

        <div className="span-md-3">
          <div className="">
            <Staff.Section border title="Client" actions={<>Modifier</>}>
              <CustomerCard data={cart.customer_data} />
            </Staff.Section>

            <Staff.Section border title="Panier" actions={cart.items.length} footer={<>Confirmer</>}>
              {cart.items.map((i) => {
                const { product, size, quantity } = i;
                return (
                  <div className="" key={i.slug}>
                    <div className="p">{product.name}</div>
                    <div className="d-flex justify-content-between">
                      <span className="">{size}</span>

                      <span className="">{quantity}</span>
                    </div>
                    <div className="">{}</div>
                  </div>
                );
              })}

              <div className="mt-4">
                <div className="">
                  <span className="">Subtotal</span>
                </div>
                <div className="">
                  <span className="">Total</span>
                </div>
              </div>
            </Staff.Section>
          </div>
        </div>
      </div>
    </Staff.View>
  );
}

export const ProductAddForm = ({
  item,
  onItemClick,
  ...props
}: {
  item: TProduct;
  onItemClick: (p: TProduct, size: string, qty: string) => Promise<TOrder>;
}) => {
  const form = useForm({ quantity: 1, size: '' });

  if (!item?.slug) return null;

  const { sizes = [], is_oos, category_data, cover_data, name, size_count = 0 } = item;

  const canAdd = !is_oos && size_count > 0;

  return (
    <Form
      context={form}
      onSubmit={(e) => {
        e.preventDefault();
        onItemClick(item, form.values.size, form.values.quantity).catch((err) => {
          form.handleError(err);
        });
      }}
    >
      <Staff.Section
        border
        header={
          <div className="d-flex">
            <div style={{ minWidth: 34, maxWidth: 50 }} className="me-1">
              <Staff.Img.Thumb src="src" {...cover_data} className="rounded" />
            </div>

            <div className="flex-1" title={name}>
              <div className="text-ellipsis text-sm">
                <span className="me-1 fw-bold">{truncateStr(name!, 0, 45)}</span>
              </div>

              <div className="d-flex flex-column flex-md-ro justify-content-between">
                <div className="d-grid grid-md-2 gap-1 flex-1">
                  <SizeSelectField required sizes={sizes.filter((i) => i.quantity > 0)} error={form.errors.size} />
                  <SizeQuantityField max={sizes.find((i) => i.slug === form.values.size)?.quantity || 0} min={1} />
                </div>
                <div className="text-right">
                  <Form.Submit className="btn btn-primary-2 btn-md fw-bold">Ajouter</Form.Submit>
                </div>
              </div>
            </div>
          </div>
        }
        actions={<ProductPriceDisplay {...item} className="d-flex flex-wrap" />}
        className="mb-1"
      ></Staff.Section>
    </Form>
  );
};
