import { ViewSection } from '@miq/componentjs';
import { useRequest } from '@miq/hookjs';
import Staff from '@miq/staffjs';
import { Response } from '@miq/utiljs';
import * as React from 'react';
import { Link } from 'react-router-dom';
import { CustomerCard } from './components';
import { Cart } from './models';
import { TCart } from './types';

export default function SalesIndexView() {
  const { res, setRes, loading, ...request } = useRequest(() => Cart().list());

  const r = new Response<TCart>(res);

  return (
    <Staff.View
      title="Sales"
      actions={
        <>
          <Staff.Link to="cart/" className="btn btn-primary">
            Ajouter une commande
          </Staff.Link>
        </>
      }
    >
      {r.items?.map((i) => {
        const { customer, items = [] } = i || {};

        return (
          <ViewSection key={i.slug}>
            <Link to={`cart/${i.slug}/`}>
              <CustomerCard data={customer} />
              {items.map((i) => {
                return (
                  <>
                    <div>{i?.product?.name}</div>
                  </>
                );
              })}
            </Link>
          </ViewSection>
        );
      })}
    </Staff.View>
  );
}
